/**
 * @time 2018年7月26日17:47:52
 * @author joke<277637411@qq.com>
 * @description 提示框
 */

var tooltip = {
	el: null,
	styles: {
		"transition": ".3 all ease",
		"backgroundColor": "#fff",
		"border": "1px solid #ccc",
		"borderRadius":"2px",
		"padding": "12px",
		"position": "absolute",
		"color": "#999",
		"maxWidth": "200px",
		"maxHeight": "160px",
		"word-break":"break-all",
		"fontFamoly": "'Helvetica Neue', 'Luxi Sans', 'DejaVu Sans', Tahoma, 'Hiragino Sans GB', STHeiti, 'Microsoft YaHei'"
	},
	arrowStyles1:{
		"position":"absolute",
		"left":"-17px",
		"top":"50%",
		"marginTop":"-8px",
		"border": "8px solid transparent",
  		"borderRight": "8px solid #ccc",
	},
	arrowStyles2:{
		"position":"absolute",
		"left":"-16px",
		"top":"50%",
		"marginTop":"-8px",
		"border": "8px solid transparent",
  		"borderRight": "8px solid #fff",
	},
	show(x, y, html) {
		var width = width || 140;
		var height = height || 60;
		this.el = document.querySelector('#d4-chart-tooltip-wrap');
		if (this.el) {
			this.el.parentNode.removeChild(this.el);
			this.el = null;
		}

		var template = `<div id="d4-chart-tooltip-001">${html}</div>`
		var div = document.createElement('div');
		var arrow1 = document.createElement('span');
		var arrow2 = document.createElement('span');

		div.id = 'd4-chart-tooltip-wrap'
		div.innerHTML = template;
		div.appendChild(arrow1);
		div.appendChild(arrow2);


		document.body.appendChild(div);
		div.style.opacity = 0;
		div.style.left = x + 24 + 'px';
		div.style.top = (y - height / 2) + 'px';
		div.style.opacity = 1;

		this.el = div;
		this._applyStyles(arrow1,this.arrowStyles1);
		this._applyStyles(arrow2,this.arrowStyles2);
		this._applyStyles(div,this.styles);
	},
	update(x, y) {
		if (this.el) {
			var width = width || 140;
			var height = height || 60;
			var wrap = this.el;
			wrap.style.left = (x + 24) + 'px';
			wrap.style.top = (y - height / 2) + 'px'
		}
	},
	_applyStyles(obj,styles) {
		for (var key in styles) {
			obj.style[key] = styles[key];
		}
	},
	hide() {
		var tooltip = document.querySelector('#d4-chart-tooltip-wrap');
		if (tooltip) tooltip.parentNode.removeChild(tooltip);
	}
}

export default tooltip;



